export default {
	data() {
		return {
			//多行列表
			lists: []
		};
	},
	onLoad() {//在加载页面时往lists页面中push 五个数 这样在循环的时候才有的循环 不然循环不出来
		let list = [];
		for (let i = 0; i < 5; i++) {
			list.push(i)
		}
		this.lists = list;
	},
	methods: {
		change(e) {
			console.log(e);
		}
	}
}

